import React, { Component } from "react";
import { ContractsAbi, ContractAdress } from "../web3/shared4";
import { MDBInput } from "mdbreact";
import aze from "../ressources/dark.png";

import Tx from "ethereumjs-tx";
 import popup from "../popup";
import popup2 from "../popup2.js";

 
class CrowdForm extends Component {
  state = {
    account: {
      name: "",
      contact: 0,
      title: "",
      dateL: 0,
      desc: "",
      amount: 0
    }
  };

  handleChange = ({ currentTarget: input }) => {
    const account = { ...this.state.account };
    account[input.name] = input.value;
    this.setState({ account });
  };

  handleSubmit = () => {
 

    let name = this.state.account.name;
    let contact = this.state.account.contact;
    let title = this.state.account.title;
    let dateL = new Date(this.state.account.dateL) / 1000;
    let desc = this.state.account.desc;
    let amount = this.state.account.amount;
    if(name==''||contact==''||title==''||dateL==''||desc==''||amount=='') {
      popup2()    } else {
    console.log("begin");

   
    //const web3 = new Web3("http://localhost:7545");
    console.log("crwd");
     const myContract = window.web3.eth.contract(ContractsAbi).at(ContractAdress);
     myContract.newProject.sendTransaction(name,contact,title,dateL,desc,amount,{
      from:  window.web3.eth.accounts[0],
    },function(error , result){
       if(!error)
           console.log(result);
       else
           console.log(error.code)
  })                                                           
  };
  }
  render() {
    const { name, contact, title, dateL, desc, amount } = this.state;
    let Background = aze;
    var sectionStyle = {
      width: "100%",
      height: "690px",
      backgroundImage: `url(${Background})`,
      backgroundAttachment: "fixed",
      backgroundPosition: "center",
      backgroundRepeat: "no-repeat",
      backgroundSize: "cover"
    };
    return (
      <section style={sectionStyle}>
        <div
          className="container center_div p-4 white rounded shadow-sm"
          style={{ width: "65%", paddingBottom: "15px" }}
        >
          <h1 className="font-weight-bold text-center fahmi red-text accent-4">
            Publish An Announcement
          </h1>
          <div className="col-md-12 mb-5">
            <form onSubmit={e => e.preventDefault()}>
              <div className="form-row">
                <div className="form-group col-md-6">
                  <MDBInput
                    label="Full Name"
                    onChange={this.handleChange}
                    value={name}
                    name="name"
                    outline
                  />
                </div>
                <div className="form-group col-md-6">
                  <MDBInput
                    label="Contact (phone_number)"
                    type="number"
                    onChange={this.handleChange}
                    value={contact}
                    name="contact"
                    outline
                  />
                </div>
                <div className="form-group col-md-6">
                  <MDBInput
                    label="Project Title"
                    onChange={this.handleChange}
                    value={title}
                    name="title"
                    outline
                  />
                </div>
                <div className="form-group col-md-6">
                  <MDBInput
                    label="Project Deadline"
                    type="date"
                    onChange={this.handleChange}
                    value={dateL}
                    name="dateL"
                    outline
                  />
                </div>
              </div>
              <div className="form-group">
                <MDBInput
                  label="Description Of The Project"
                  type="textarea"
                  style={{ width: "100%" }}
                  value={desc}
                  name="desc"
                  onChange={this.handleChange}
                  outline
                />
              </div>

              <div
                className="form-group"
                style={{
                  textAlign: "center",
                  marginLeft: "150px",
                  marginRight: "150px"
                }}
              >
                <MDBInput
                  label="Amount Wanted"
                  type="number"
                  onChange={this.handleChange}
                  value={amount}
                  name="amount"
                  outline
                />
              </div>

              <button
                type="submit"
                className="btn btn-success py-2 px-4 btn-block text-white fahmi"
                onClick={this.handleSubmit}
              >
                Submit
              </button>
            </form>
          </div>
        </div>
      </section>
    );
  }
}

export default CrowdForm;
